<template>
  <view class="result-page">
    <!-- È°∂ÈÉ®Ê®°ÂºèÊ†áÈ¢ò -->
    <view class="mode-header" :style="{backgroundColor: modeBgColor}">
      <text class="mode-title">{{modeTitle}}</text>
    </view>

    <!-- Ê†∏ÂøÉÊï∞ÊçÆÂç°Áâá -->
    <view class="result-card">
      <!-- Ë≠¶Âä°‰ΩìËÉΩÊµãËØïÊ®°ÂºèÔºö‰∏çÊòæÁ§∫ÈÄöÁî®ÁöÑËøêÂä®Êó∂Èïø/Ë∑ùÁ¶ªÔºåËÄåÊòØÊòæÁ§∫È°πÁõÆ‰∏ìÂ±ûÊï∞ÊçÆ -->
      <view class="base-data" v-if="currentMode === 'test'">
        <text class="base-title">ÊµãËØïÈ°πÁõÆÔºö{{ testProject }}</text>
        <view class="data-item">
          <text class="item-label">ÂÆåÊàêÊï∞Èáè</text>
          <text class="item-value count-text">{{ testCount }} Ê¨°</text>
        </view>
        <view class="data-item">
          <text class="item-label">ÊµãËØïÁî®Êó∂</text>
          <text class="item-value">{{ formatDuration(duration) }}</text>
        </view>
        <view class="data-item">
          <text class="item-label">Âä®‰ΩúÂà§ÂÆö</text>
          <text class="item-value" :class="testQualified ? 'success' : 'fail'">{{ testQualified ? '‚úÖ ÂêàÊ†º' : '‚ùå Êú™ÂêàÊ†º' }}</text>
        </view>
      </view>

      <!-- Test Mode Analysis Section -->
      <view class="mode-data" v-if="currentMode === 'test'">
        <text class="mode-data-title">ÊàêÁª©ÂàÜÊûê</text>
        <view class="bar-chart">
          <view class="bar-row">
            <text class="bar-label">ÊàëÁöÑ</text>
            <view class="bar-track">
              <view class="bar-fill user-fill" :style="{width: userScorePercent + '%'}"></view>
              <text class="bar-val-in" v-if="userScorePercent > 15">{{testCount}}</text>
            </view>
            <text class="bar-val-out" v-if="userScorePercent <= 15">{{testCount}}</text>
          </view>
          <view class="bar-row">
            <text class="bar-label">ÂêàÊ†º</text>
            <view class="bar-track">
              <view class="bar-fill standard-fill" :style="{width: standardScorePercent + '%'}"></view>
              <text class="bar-val-in" v-if="standardScorePercent > 15">{{standardReq}}</text>
            </view>
            <text class="bar-val-out" v-if="standardScorePercent <= 15">{{standardReq}}</text>
          </view>
        </view>
        
        <view class="suggestion-box">
          <text class="sugg-title">üí° Êô∫ËÉΩÂèçÈ¶à</text>
          <text class="sugg-text">{{suggestionText}}</text>
        </view>
      </view>

      <!-- ÈÄöÁî®Âü∫Á°ÄÊï∞ÊçÆÔºàÈùûÊµãËØïÊ®°ÂºèÊòæÁ§∫Ôºâ -->
      <view class="base-data" v-else>
        <text class="base-title">ËøêÂä®Âü∫Á°ÄÊï∞ÊçÆ</text>
        <view class="data-item">
          <text class="item-label">ËøêÂä®Êó∂Èïø</text>
          <text class="item-value">{{formatDuration(duration)}}</text>
        </view>
        <view class="data-item">
          <text class="item-label">ËøêÂä®Ë∑ùÁ¶ª</text>
          <text class="item-value">{{(distance/1000).toFixed(2)}} ÂÖ¨Èáå</text>
        </view>
      </view>

      <!-- Ê®°Âºè‰∏ìÂ±ûÊï∞ÊçÆ -->
      <view class="mode-data" v-if="currentMode === 'police'">
        <text class="mode-data-title">Ë≠¶Âä°‰∏ìÈ°πËÄÉÊ†∏Êï∞ÊçÆ</text>
        <!-- 2000Á±≥ÂÆåÊàêÁä∂ÊÄÅ -->
        <view class="data-item">
          <text class="item-label">2000Á±≥ÁõÆÊ†áÂÆåÊàê</text>
          <text class="item-value" :class="isPoliceFinish ? 'success' : 'fail'">
            {{isPoliceFinish ? '‚úÖ Â∑≤ÂÆåÊàê' : '‚ùå Êú™ÂÆåÊàê'}}
          </text>
        </view>
        <!-- ÈÖçÈÄüËææÊ†áÁä∂ÊÄÅ -->
        <view class="data-item">
          <text class="item-label">ËÄÉÊ†∏ÈÖçÈÄüËææÊ†á</text>
          <text class="item-value" :class="isPaceQualified ? 'success' : 'fail'">
            {{isPaceQualified ? `‚úÖ ËææÊ†áÔºà${policePace.toFixed(1)} ÂàÜ/ÂÖ¨ÈáåÔºâ` : `‚ùå Êú™ËææÊ†áÔºà${policePace.toFixed(1)} ÂàÜ/ÂÖ¨ÈáåÔºâ`}}
          </text>
        </view>
        <!-- ËÄÉÊ†∏Ê†áÂáÜÂèÇËÄÉ -->
        <view class="data-item tips">
          <text class="item-label">ËÄÉÊ†∏Ê†áÂáÜ</text>
          <text class="item-value">Áî∑Áîü‚â§6.5ÂàÜ/ÂÖ¨Èáå | Â•≥Áîü‚â§7.5ÂàÜ/ÂÖ¨Èáå</text>
        </view>
      </view>

      <view class="mode-data" v-if="currentMode === 'campus'">
        <text class="mode-data-title">Ê†°Âõ≠ÊâìÂç°Êï∞ÊçÆ</text>
        <view class="data-item">
          <text class="item-label">ÊâìÂç°ÁÇπÂà∞ËææÁä∂ÊÄÅ</text>
          <text class="item-value" :class="isReach ? 'success' : 'fail'">
            {{isReach ? '‚úÖ Â∑≤Âà∞Ëææ' : '‚ùå Êú™Âà∞Ëææ'}}
          </text>
        </view>
      </view>

      <view class="mode-data" v-if="currentMode === 'normal'">
        <text class="mode-data-title">ÊôÆÈÄöË∑ëÊ≠•Êï∞ÊçÆ</text>
        <view class="data-item tips">
          <text class="item-label">Ê∏©È¶®ÊèêÁ§∫</text>
          <text class="item-value">Êï∞ÊçÆÂ∑≤Ëá™Âä®ËÆ∞ÂΩïÔºåÂèØÂú®„ÄåÊàëÁöÑ„ÄçÈ°µÈù¢Êü•ÁúãÊ±áÊÄª</text>
        </view>
      </view>
    </view>

    <!-- Êìç‰ΩúÊåâÈíÆÂå∫ -->
    <view class="btn-group">
      <button @click="saveRunRecord" class="save-btn" :disabled="isSaved || isSaving">
        {{isSaved ? '‚úÖ Êï∞ÊçÆÂ∑≤‰øùÂ≠ò' : (isSaving ? 'Ê≠£Âú®‰øùÂ≠ò...' : '‰øùÂ≠òËøêÂä®Êï∞ÊçÆ')}}
      </button>
      <button @click="backToHome" class="back-btn">ËøîÂõûÈ¶ñÈ°µ</button>
    </view>
  </view>
</template>

<script setup>
// Áªü‰∏ÄÂØºÂÖ•ËßÑËåÉ
import { ref, computed} from 'vue';
import { onShow, onLoad  } from '@dcloudio/uni-app';

// 1. Êé•Êî∂Ë∑ëÊ≠•È°µ‰º†ÈÄíÁöÑÂèÇÊï∞
const currentMode = ref('normal'); // normal/police/campus
const duration = ref(0); // ËøêÂä®Êó∂ÈïøÔºàÁßíÔºâ
const distance = ref(0); // ËøêÂä®Ë∑ùÁ¶ªÔºàÁ±≥Ôºâ
const isReach = ref(false); // Ê†°Âõ≠ÊâìÂç°ÊòØÂê¶Âà∞Ëææ
const isPoliceFinish = ref(false); // Ë≠¶Âä°2000Á±≥ÊòØÂê¶ÂÆåÊàê
const policePace = ref(0); // Ë≠¶Âä°ÈÖçÈÄüÔºàÂàÜ/ÂÖ¨ÈáåÔºâ
// ÊµãËØïÊ®°Âºè‰∏ìÂ±û
const testProject = ref('');
const testType = ref('');
const testCount = ref(0);
const testQualified = ref(false);
const standardReq = ref(0);
const userScorePercent = ref(0);
const standardScorePercent = ref(0);
const suggestionText = ref('');

// 2. Êú¨Âú∞Áä∂ÊÄÅ
const isSaved = ref(false); // Êï∞ÊçÆÊòØÂê¶Â∑≤‰øùÂ≠ò
const isSaving = ref(false); // Êï∞ÊçÆÊòØÂê¶Ê≠£Âú®‰øùÂ≠ò‰∏≠

// 3. ËÆ°ÁÆóÂ±ûÊÄßÔºöÂä®ÊÄÅÊ†áÈ¢òÂíåËÉåÊôØËâ≤
const modeTitle = computed(() => {
  switch (currentMode.value) {
    case 'police': return 'üö® Ë≠¶Âä°‰∏ìÈ°π2000Á±≥ËÆ≠ÁªÉÁªìÁÆó';
    case 'campus': return 'üè´ Ê†°Âõ≠ÊâìÂç°Ë∑ëÊ≠•ÁªìÁÆó';
    case 'test': return 'üí™ Ë≠¶Âä°‰ΩìËÉΩÊµãËØïÁªìÁÆó';
    default: return 'üèÉ ÊôÆÈÄöË∑ëÊ≠•ÁªìÁÆó';
  }
});
const modeBgColor = computed(() => {
  switch (currentMode.value) {
    case 'police': return '#fdf2f0';
    case 'campus': return '#e8f4f8';
    case 'test': return '#f3f7ff';
    default: return '#f5f5f5';
  }
});
// Ë≠¶Âä°ÈÖçÈÄüÊòØÂê¶ËææÊ†áÔºàÁî∑Áîü6.5ÔºåÂ•≥Áîü7.5ÔºåËøôÈáåÈªòËÆ§ÊåâÁî∑ÁîüÊ†áÂáÜÔºâ
const isPaceQualified = computed(() => {
  return policePace.value <= 6.5;
});

// 4. È°µÈù¢Âä†ËΩΩÊó∂Êé•Êî∂ÂèÇÊï∞
onLoad((options) => {
  currentMode.value = options.mode || 'normal';
  duration.value = Number(options.duration) || 0;
  distance.value = Number(options.distance) || 0;
  isReach.value = options.isReach === 'true';
  isPoliceFinish.value = options.isPoliceFinish === 'true';
  policePace.value = Number(options.policePace) || 0;
  if (currentMode.value === 'test') {
    testProject.value = options.project || '';
    testCount.value = Number(options.count) || 0;
    // Êò†Â∞ÑÊµãËØïÁ±ªÂûã
    const map = { 'Âºï‰ΩìÂêë‰∏ä': 'pull_up', '‰ª∞ÂçßËµ∑Âùê': 'sit_up', '‰øØÂçßÊíë': 'push_up' };
    testType.value = map[testProject.value] || '';
    // ÁÆÄÊòìÂêàÊ†ºÂà§ÂÆöÔºàmockËßÑÂàôÔºâ
    const threshold = { pull_up: 10, sit_up: 40, push_up: 30 };
    const req = threshold[testType.value] || 0;
    standardReq.value = req;
    testQualified.value = testCount.value >= req;
    
    // ËÆ°ÁÆóÁôæÂàÜÊØîÔºà‰ª•Ê†áÂáÜÂÄºÁöÑ1.5ÂÄç‰∏∫Êª°Êù°Ôºâ
    const maxVal = Math.max(req * 1.5, testCount.value * 1.2, 1);
    userScorePercent.value = Math.min((testCount.value / maxVal) * 100, 100);
    standardScorePercent.value = Math.min((req / maxVal) * 100, 100);
    
    // ÁîüÊàêÂª∫ËÆÆ
    if (testQualified.value) {
      suggestionText.value = 'ÊÅ≠ÂñúËææÊ†áÔºÅÂª∫ËÆÆ‰øùÊåÅÂΩìÂâçÁöÑËÆ≠ÁªÉÈ¢ëÁéáÔºåÈÄÇÂΩìÂ¢ûÂä†Ê†∏ÂøÉÂäõÈáèËÆ≠ÁªÉ‰ª•Ëøõ‰∏ÄÊ≠•ÊèêÂçáÁ®≥ÂÆöÊÄß„ÄÇ';
    } else {
      const diff = req - testCount.value;
      suggestionText.value = `Ë∑ùÁ¶ªËææÊ†áËøòÂ∑Æ ${diff} Ê¨°„ÄÇÂª∫ËÆÆÂä†Âº∫‰∏äËÇ¢ÂäõÈáèËÆ≠ÁªÉÔºåÊØèÂ§©ËøõË°å3ÁªÑËæÖÂä©ÁªÉ‰π†ÔºåÊ≥®ÊÑèÂä®‰ΩúËßÑËåÉ„ÄÇ`;
    }
  }
});

// 5. Ê†ºÂºèÂåñÊó∂ÈïøÔºàÁßí ‚Üí ÂàÜ:ÁßíÔºâ
const formatDuration = (seconds) => {
  const min = Math.floor(seconds / 60);
  const sec = seconds % 60;
  return `${min.toString().padStart(2, '0')}:${sec.toString().padStart(2, '0')}`;
};

// 6. ‰øùÂ≠òÊï∞ÊçÆÔºàÊú¨Âú∞+‰∫ëÁ´ØÔºâ
const saveRunRecord = () => {
  if (isSaved.value || isSaving.value) return;

  isSaving.value = true;
  uni.showLoading({ title: '‰øùÂ≠ò‰∏≠...' });

  // 6.1 Êú¨Âú∞ÁºìÂ≠òÊõ¥Êñ∞ÔºàÂå∫ÂàÜÁ±ªÂûãÔºâ
  if (currentMode.value !== 'test') {
    const totalCount = Number(uni.getStorageSync('totalRunCount')) || 0;
    uni.setStorageSync('totalRunCount', totalCount + 1);
    const totalDistance = Number(uni.getStorageSync('totalRunDistance')) || 0;
    uni.setStorageSync('totalRunDistance', totalDistance + distance.value / 1000);
    if (currentMode.value === 'campus' && isReach.value) {
      const successCount = Number(uni.getStorageSync('successCampusCount')) || 0;
      uni.setStorageSync('successCampusCount', successCount + 1);
    }
    if (currentMode.value === 'police' && isPoliceFinish.value && isPaceQualified.value) {
      const policeSuccessCount = Number(uni.getStorageSync('policeSuccessCount')) || 0;
      uni.setStorageSync('policeSuccessCount', policeSuccessCount + 1);
    }
  }
  // ‰øùÂ≠òËøêÂä®ËÆ∞ÂΩïÂà∞ÂàóË°®ÔºàÁªü‰∏ÄÂàóË°®ÔºåÊñ∞Â¢ûtypeÂ≠óÊÆµÔºâ
  const runRecordsList = uni.getStorageSync('runRecordsList') || [];
  if (currentMode.value === 'test') {
    runRecordsList.push({
      type: 'police_test',
      testType: testType.value,
      testName: testProject.value,
      testCount: testCount.value,
      result: testQualified.value ? 'ÂêàÊ†º' : 'Êú™ÂêàÊ†º',
      duration: duration.value,
      createTime: new Date().getTime()
    });
  } else {
    const km = distance.value / 1000;
    const min = duration.value / 60;
    const pace = km === 0 ? 0 : (min / km);
    runRecordsList.push({
      type: 'run',
      mode: currentMode.value,
      duration: duration.value,
      distance: distance.value / 1000,
      pace: pace,
      isReach: isReach.value,
      isPoliceFinish: isPoliceFinish.value,
      policePace: policePace.value,
      isPaceQualified: isPaceQualified.value,
      createTime: new Date().getTime()
    });
  }
  uni.setStorageSync('runRecordsList', runRecordsList);

  // 6.2 ‰∫ëÁ´ØÂêåÊ≠•ÔºàÈúÄÂºÄÈÄöUniCloudÔºâ
  const db = uniCloud.database();
  const runRecords = db.collection('run_records');
  const basePayload = { userId: uni.getStorageSync('bindDeviceId') || 'test_user', duration: duration.value, createTime: new Date().getTime() };
  const payload = currentMode.value === 'test'
    ? { ...basePayload, type: 'police_test', testType: testType.value, testName: testProject.value, testCount: testCount.value, result: testQualified.value ? 'ÂêàÊ†º' : 'Êú™ÂêàÊ†º' }
    : { ...basePayload, type: 'run', mode: currentMode.value, distance: distance.value / 1000, isReach: isReach.value, isPoliceFinish: isPoliceFinish.value, policePace: policePace.value, isPaceQualified: isPaceQualified.value };
  
  runRecords.add(payload).then(() => {
    isSaved.value = true;
    uni.showToast({ title: '‚úÖ Êï∞ÊçÆ‰øùÂ≠òÊàêÂäü', icon: 'success' });
  }).catch((err) => {
    console.error('‰∫ëÁ´Ø‰øùÂ≠òÂ§±Ë¥•Ôºö', err);
    isSaved.value = true;
    uni.showToast({ title: '‚ö†Ô∏è Êú¨Âú∞‰øùÂ≠òÊàêÂäüÔºå‰∫ëÁ´ØÂêåÊ≠•Â§±Ë¥•', icon: 'none' });
  }).finally(() => {
    isSaving.value = false;
    uni.hideLoading();
  });
};

// 7. ËøîÂõûÈ¶ñÈ°µ
const backToHome = () => {
  uni.redirectTo({ url: '/pages/home/home' });
};
</script>

